.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

#root .box{
      margin: 20px auto;
		width: 500px;
		min-height: 460px;
		background: #f5f5f5;
		border: 1px solid #ccc;
		border-radius: 4px;
}
#root .box .head{
	  font-size:26px ;
		text-align: center;
		margin: 30px auto 15px;
}
#root .box .list{
	width: 400px;
    		height: 203px;
    		background: #fff;
    		border-radius: 2px;
    		border: 1px solid #CCCCCC;
    		margin: 0 auto;
    		overflow: auto;
}
#root .box .list .listItem{
	height: 40px;
    		border-bottom: 1px solid #CCCCCC;
    		line-height: 40px;
    			-webkit-transition: 0.5s;
    			-o-transition: 0.5s;
    			transition: 0.5s;
    			overflow: hidden;
}
#root .box .list .listItem input{
	margin: 0 10px 0 15px;
	cursor: pointer;
}
#root .box .list .listItem .txt{
	display: inline-block;
	cursor: pointer;
}
#root .box .list .listItem .button{
	width: 40px;
	height: 20px;
	line-height: 20px;
	text-align: center;
	background: #327cbd;
	background-color: rgba(50,124,189,0.7);
	border: none;
	border-radius: 6px;
	color: white;
	float: right;
	margin-top:12px;
	margin-right: 10px;
	cursor: pointer;
	font-size: 12px;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}
#root .box .list .listItem .button:hover{
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
	background-color: rgba(50,124,189,1);
}
#root .box .list .checked{
	background: #dff1d9;
} 
#root .box .list .checked .txt{
	text-decoration: line-through;
}
.finish{
	width: 390px;
	height: 40px;
	line-height: 40px;
	padding-left: 10px;
	margin: 0 auto;
	color: gray;
}
.addBox{
	width: 400px;
	margin: 10px auto;
	display: -ms-flexbox;
	display: flex;
}
.addBox .inputTxt{
	height: 30px;
	width: 300px;
	border-radius: 2px;
	border: 1px solid #CCCCCC;
	padding-left: 20px;
}
.addBox  button{
	background: #327cbd;
	border:none;
	width: 60px;
	height: 30px;
	margin-left: 18px;
	color: #fff;
	border-radius: 4px;
	cursor: pointer;
	background-color: rgba(50,124,189,0.7);
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}
.addBox  button:hover{
	background-color: rgba(50,124,189,1);
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}
.searchBox{
	width: 400px;
	height: 30px;
	margin: 0 auto;
	margin-bottom: 10px;
}
.searchBox p{
	height: 30px;
	width: 180px;
	line-height: 30px;
	margin: 0;
	overflow: auto;
	}
	.searchBox input{
		height: 30px;
		width: 300px;
		padding-left: 20px;
		border: 1px solid #ccc;
		border-radius: 2px;
	}
.searchBox button{
	background: #327cbd;
	background-color: rgba(50,124,189,0.7);
	height: 30px;
	width: 58px;
	border: none;
	color: white;
	border-radius: 4px;
	margin: 2px 0px 2px 18px;
	cursor: pointer;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}	
.searchBox button:hover{
	background-color: rgba(50,124,189,1);
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}
.searchBox p{
	font-size: 14px;
	margin-top: 4px;
}



